	module.exports = {"data":{"wordpressWpLessons":{"id":"fe8fc567-ab00-5803-9bad-cab221a08237","wordpress_id":117,"title":"Game Development Mini Project","slug":"game-development-mini-project","content":"<h2>Project Overview</h2>\n<p>For the Game Development Mini Project, you&#8217;re going to create a game or interactive experience in Scratch. The project has three phases: planning, creating, and documenting. You are able to work in a team or on your own. However, if you work in a team, each member will be responsible for having the planning and documentation!</p>\n<p>Before beginning your project, create a <strong>wireframe/plan</strong> and <strong>level layout</strong> for the game. A wireframe is a basic sketch of your game interface. You may, of course, deviate from this plan, but it&#8217;s an important step in the design process! Then, create your project. Once you are done, you will follow the instructions at the bottom of this post to create the Maker&#8217;s Reflection document.</p>\n<p>Please focus on making sure these projects are solid and well documented. Completing and documenting your projects is required for DHF Membership.</p>\n<h2>Project Prompt</h2>\n<p><strong><span style=\"color: #cf0000;\">IMPORTANT: Please read this ENTIRE section before starting!</span></strong></p>\n<p>The project prompt for <strong>Game Development</strong> is going to be slightly different than the previous prompt for Graphic Design. This time, you will be able to choose between 3 different prompts</p>\n<ul>\n<li><strong>Create a platformer game</strong></li>\n<li><strong>Create a scroller game</strong></li>\n<li><strong>Create an interactive experience</strong></li>\n</ul>\n<p>Regardless of which prompt you choose, your project should demonstrate that you understand some core programming/coding concepts. It must include the following blocks/concepts:</p>\n<ul>\n<li><strong>Loop</strong>: This can be the <em>repeat</em> or <em>forever</em> brick</li>\n<li><strong>If statement</strong>: Include <em>at least one</em> conditional</li>\n<li><strong>Broadcasts/listeners</strong></li>\n<li><strong>Motion/animation</strong>: Any type of motion</li>\n<li><strong>KeyPress</strong></li>\n</ul>\n<p>If you choose to create a <strong>Platformer</strong>, you should also include:</p>\n<ul>\n<li><strong>Collision Detection</strong>: Use any type of sensor for detection</li>\n<li><strong>Gravity</strong>: Use a variable to control gravity within your game world</li>\n</ul>\n<p>If you choose to create a <strong>Scroller</strong>, you should also include:</p>\n<ul>\n<li><strong>Clones</strong>: Have sprites in your game duplicate themselves</li>\n<li><strong>Multiple backdrops</strong>: Move through different environments</li>\n</ul>\n<p>If you choose to create an <strong>Interactive Experience</strong>, your project may include a variety of the concepts/blocks mentioned above, depending on how you want your project to function. Some examples of interactive experiences are:</p>\n<ul>\n<li>A <a href=\"https://scratch.mit.edu/projects/169798499/\">Drawing Game</a></li>\n<li><a href=\"https://scratch.mit.edu/projects/71366816/\">Choose Your Own Adventure</a></li>\n<li>An <a href=\"https://scratch.mit.edu/projects/20974745/\">Animation</a></li>\n</ul>\n<p>Feel free to include much more than these concepts/blocks in your project! The mini-project is completely open to your creativity, just make sure to include the above concepts.</p>\n<p><b>Remember:</b></p>\n<ul>\n<li>Save your work!\n<ul>\n<li>The first time you save your project, use the <span style=\"color: #0091c9;\">File -&gt; Save As</span> option and give your document a name and save it to the &#8220;Documents&#8221; folder instead of the default location. While you are working, save your work every once in a while under <span style=\"color: #0091c9;\">File -&gt; Save</span>. When you are all done, upload the work to Google Drive.</li>\n</ul>\n</li>\n<li>Take screenshots!\n<ul>\n<li>You can create screenshots to document your project as you&#8217;re working as well as upload the final product. This is a good skill to practice as you&#8217;ll want to get in the habit of documenting a project as you&#8217;re working on it. Also, feel free to take pictures of any sketches that you&#8217;ve done in your Maker Notebook!</li>\n</ul>\n</li>\n</ul>\n<h2>Maker&#8217;s Reflection</h2>\n<p>Answer these 3 questions in your reflection.</p>\n<ul>\n<li>What was the project prompt?</li>\n<li>What is your project?</li>\n<li>Why did you make this project?</li>\n</ul>\n<p>Then, choose at least 2 from the list below to respond to.</p>\n<ul>\n<li>How did you plan or prototype your project before starting?</li>\n<li>How did you make it? What was your process? What steps did you follow?</li>\n<li>What problems or challenges did you face?</li>\n<li>How did you overcome any challenges and solve problems that you met?</li>\n<li>What would you do differently next time?</li>\n<li>What would you tell someone else who was going to make this project?</li>\n<li>What did you like best about your project?</li>\n<li>How would you make it better?</li>\n</ul>\n"}},"pathContext":{}}

